

const use_root = { root: true }


function nop() { }

////////////////////////////////////////////////////////////////////////////////

const state = {

    repositories: []

};

const getters = {

    list(state) {
        return state.repositories
    }

}

const mutations = {

    handle_search_result(state, vo) {
        if (vo == null) { return }
        let repositories = vo.local_repositories
        if (repositories == null) { return }
        state.repositories = repositories
    },

    reset(state) {
        state.repositories = []
    },

};

const actions = {
    search(context, p) {

        let path = p.path

        let method = 'POST'
        let type = 'import-repositories'
        let data = {
            action: "find",
            local_repositories: [{ path }]
        }
        let task = context.dispatch("rest/execute", { method, type, data }, use_root)
        task.then((r) => {
            let vo = r.data;
            context.commit('handle_search_result', vo)
        })
        return task;
    },

    locate(context, p) {

        let path = p.path

        let method = 'POST'
        let type = 'import-repositories'
        let data = {
            action: "locate",
            local_repositories: [{ path }]
        }
        let task = context.dispatch("rest/execute", { method, type, data }, use_root)
        task.then((r) => {
            let vo = r.data;
            context.commit('handle_search_result', vo)
        })
        return task;
    },

    save(context, { items }) {

        nop(items)

        let method = 'POST'
        let type = 'import-repositories'
        let data = {
            action: "save",
            local_repositories: items
        }
        let task = context.dispatch("rest/execute", { method, type, data }, use_root)
        task.then((r) => {
            let vo = r.data;
            context.commit('handle_repositories', vo)
        })
        return task;
    },
};



export default {
    name: "module_import_local_repository",
    namespaced: true,
    state, mutations, actions, getters
}
